module.exports = "function renderTooltip(props) {\n  return (\n    <Tooltip id=\"button-tooltip\" {...props}>\n      Simple tooltip\n    </Tooltip>\n  );\n}\n\nconst Example = () => (\n  <OverlayTrigger\n    placement=\"right\"\n    delay={{ show: 250, hide: 400 }}\n    overlay={renderTooltip}\n  >\n    <Button variant=\"success\">Hover me to see</Button>\n  </OverlayTrigger>\n);\n\nrender(<Example />);\n"